@import './vars';
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;500&display=swap');

* {
  color: $black;
  font-weight: 300;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

html, body {
  -ms-overflow-style: none;
  scrollbar-width: none;
  background: $background; }

#root {
  overflow: hidden; }

::-webkit-scrollbar {
  display: none; }

body {
  overscroll-behavior-y: none; }

h1 {
  margin: 0;
  font-family: "Playfair Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

h2, h3, h4, h5, h6 {
  margin: 0;
  font-weight: 500;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; }

.section-title {
  font-size: 4rem;
  line-height: 100%; }

.section-trailing {
  font-size: 2rem;
  font-weight: bold;
  color: $red;
  line-height: 90%;
  margin-left: 20px; }

.section-subtitle {
  font-size: 1.5rem;
  color: $red;
  font-weight: bold;
  line-height: 100%; }

.flex {
  display: flex;
  align-items: center; }

.flex-end {
  justify-content: flex-end; }
.flex-reverse {
  flex-direction: row-reverse; }
.overflow-hidden {
  overflow: hidden; }

.smooth-scroll {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh; }

@media only screen and (max-width: 900px) {
  .section-title {
    font-size: 2rem; }
  .section-trailing {
    font-size: 1.5rem; }
  .section-subtitle {
    font-size: 1.2rem; } }
