@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;500&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}img{user-drag:none;user-select:none;-moz-user-select:none;-webkit-user-drag:none;-webkit-user-select:none;-ms-user-select:none}

*{color:#3B393C;font-weight:300;font-family:'Poppins', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"}html,body{-ms-overflow-style:none;scrollbar-width:none;background:#FCFBF6}#root{overflow:hidden}::-webkit-scrollbar{display:none}body{overscroll-behavior-y:none}h1{margin:0;font-family:"Playfair Display", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"}h2,h3,h4,h5,h6{margin:0;font-weight:500;font-family:"Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"}.section-title{font-size:4rem;line-height:100%}.section-trailing{font-size:2rem;font-weight:bold;color:#CA424F;line-height:90%;margin-left:20px}.section-subtitle{font-size:1.5rem;color:#CA424F;font-weight:bold;line-height:100%}.flex{display:flex;align-items:center}.flex-end{justify-content:flex-end}.flex-reverse{flex-direction:row-reverse}.overflow-hidden{overflow:hidden}.smooth-scroll{overflow:hidden;position:fixed;top:0;left:0;height:100vh}@media only screen and (max-width: 900px){.section-title{font-size:2rem}.section-trailing{font-size:1.5rem}.section-subtitle{font-size:1.2rem}}

#fixed-links{width:100%;height:100vh;position:fixed;bottom:0;display:flex;align-items:flex-end;justify-content:space-between;max-width:1920px;left:50%;-webkit-transform:translate(-50%, 0);transform:translate(-50%, 0);padding-left:112px;padding-right:112px;box-sizing:border-box;z-index:2;-webkit-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}#fixed-links .left-links{display:flex;flex-direction:column;display:flex;align-items:center;justify-content:center;flex-direction:column;pointer-events:auto}#fixed-links .left-links .vertical-icons{display:flex;flex-direction:column}#fixed-links .left-links .vertical-icons a{cursor:pointer}#fixed-links .left-links .vertical-icons a .icon{margin:0 0 20px 0;width:24px;height:24px}#fixed-links .left-links .vertical-icons a .icon:hover{stroke:#CA424F}#fixed-links .right-links{display:flex;align-items:center;justify-content:center;flex-direction:column;pointer-events:auto}#fixed-links .right-links .vertical-email{margin:20px 0}#fixed-links .right-links .vertical-email a{cursor:pointer;display:block}.vertical-line{height:100px;width:1px;background:#3B393C}@media only screen and (max-width: 900px){#fixed-links{display:none}}

#menu{z-index:9;width:100%;height:100vh;position:fixed;background-color:rgba(59,57,60,0.98);display:flex;align-items:center;justify-content:center;text-align:center;-webkit-transform:translate3d(100%, 0, 0);transform:translate3d(100%, 0, 0);transition:0.3s all ease-in-out;touch-action:none}#menu .close-menu{position:absolute;display:flex;align-items:center;justify-content:center;width:60px;height:60px;right:40px;top:35px;cursor:pointer;font-size:inherit;border:none;outline:none;background:none}#menu ul{margin:0;padding:0}#menu ul li{list-style:none;font-size:2rem;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;padding:8px 0}#menu ul li button.anchor{font-size:inherit;border:none;outline:none;cursor:pointer;text-decoration:none;background:none;color:#FCFBF6;transition:0.2s all ease-in-out}#menu ul li button.anchor:hover{color:#CA424F}#menu ul li button.resume{background:#CA424F;border:none;border-radius:5px;box-shadow:0px 14px 20px 0px rgba(202,66,79,0.15);padding:15px 30px;color:#FCFBF6;font-size:1.5rem;cursor:pointer;transition:0.2s all ease-in-out}#menu ul li button.resume:hover{box-shadow:0px 14px 20px 0px rgba(202,66,79,0.4)}#menu ul li:last-child{margin-top:30px}.menu-visible{-webkit-transform:translate3d(0, 0, 0) !important;transform:translate3d(0, 0, 0) !important;box-shadow:0px 14px 20px 0px rgba(59,57,60,0.05)}

#noise-overlay{background:url(data:image/png;base64,);width:100%;z-index:3;position:absolute;top:0;left:0;-webkit-user-select:none;-ms-user-select:none;user-select:none;pointer-events:none}

nav{width:100%;max-width:1920px;margin:0 auto;position:absolute;top:0;left:0;height:128px;padding:0 112px;box-sizing:border-box;display:flex;align-items:center;justify-content:space-between;-webkit-user-select:none;-ms-user-select:none;user-select:none;z-index:2}nav .menu{display:none;width:60px;height:60px}nav .links ul li{list-style:none;margin:0 30px}nav .links ul li button.anchor{font-size:1rem;border:none;outline:none;cursor:pointer;text-decoration:none;background:none;color:inherit;transition:0.2s all ease-in-out}nav .links ul li button.anchor:hover{color:#CA424F}nav .curriculum button{background:#CA424F;border:none;border-radius:5px;box-shadow:0px 14px 20px 0px rgba(202,66,79,0.15);padding:15px 30px;color:#FCFBF6;font-size:1rem;cursor:pointer;transition:0.2s all ease-in-out}nav .curriculum button:hover{box-shadow:0px 14px 20px 0px rgba(202,66,79,0.4)}@media only screen and (max-width: 900px){nav{height:128px;padding:0 40px}nav .links{display:none}nav .curriculum button{display:none}nav .menu{cursor:pointer;display:flex;align-items:center;justify-content:center;border:none;outline:none;cursor:pointer;background:none}}

#contact{position:relative;min-height:100vh;padding:0 40px;-webkit-user-select:none;-ms-user-select:none;user-select:none;display:flex;justify-content:center;flex-direction:column;box-sizing:border-box}#contact .container{max-width:800px;margin:0 auto 80px auto}#contact p.description{font-size:2rem}#contact a button{background:#CA424F;border:none;border-radius:5px;box-shadow:0px 14px 20px 0px rgba(202,66,79,0.15);padding:15px 30px;color:#FCFBF6;font-size:1rem;cursor:pointer;transition:0.2s all ease-in-out}#contact a button:hover{box-shadow:0px 14px 20px 0px rgba(202,66,79,0.4)}.text-align-center{text-align:center}@media only screen and (max-width: 900px){#contact{padding:0 40px}#contact p.description{font-size:1.5rem}}@media only screen and (max-width: 350px){#contact{padding:0 40px}}

footer{display:flex;justify-content:center;align-items:center;flex-direction:column;padding:0 0 60px 0;-webkit-user-select:none;-ms-user-select:none;user-select:none;position:absolute;bottom:0;left:0;width:100%}footer .links{display:none}footer .links .icon{margin:10px 10px;width:24px;height:24px}footer .links .icon:hover{stroke:#CA424F}footer .credit{font-size:1rem}@media only screen and (max-width: 900px){footer .links{display:flex}}

#experience{padding:100px 192px 0 192px;min-height:100vh;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-sizing:border-box;display:flex;justify-content:center;flex-direction:column}#experience .experience-layout{margin:80px 0;display:grid;grid-gap:24px;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-template-rows:auto 1fr auto}#experience .experience-layout .experience-item{padding:0 0 0 0;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;display:flex;align-content:center;justify-content:center;flex-direction:column;text-align:center;overflow:hidden;cursor:pointer;height:280px;background:white;border:none;outline:none;border-radius:20px;z-index:3;box-shadow:0px 14px 20px 0px rgba(59,57,60,0.05);padding:50px;box-sizing:border-box;transition:0.2s all ease-in-out}#experience .experience-layout .experience-item:hover{box-shadow:0px 14px 20px 0px rgba(59,57,60,0.15)}#experience .experience-layout .experience-item img{max-height:200px;height:100%;width:100%;object-fit:contain;pointer-events:none}@media only screen and (max-width: 900px){#experience{padding:100px 40px 0 40px}}

#projects{padding:100px 192px 25vh 192px;min-height:100vh;-webkit-user-select:none;-ms-user-select:none;user-select:none}#projects .flex{position:relative}#projects .flex .column{width:50%}#projects .flex .column .description{font-size:2rem}#projects .flex .column .image-container{border-radius:20px;background:#3B393C;width:80%;height:500px;overflow:hidden}#projects .flex .column .image-container img{object-fit:cover;opacity:0.5;width:100%;height:100%}#projects .flex .column .circle{z-index:-1;width:25vw;height:25vw;position:absolute;background:#CA424F;border-radius:50%;box-shadow:0px 14px 20px 0px rgba(202,66,79,0.15)}#projects .flex .column .circle-right{right:-15vw;bottom:-8vw}#projects .flex .column .circle-left{left:-15vw;bottom:-8vw}#projects .flex .column .technologies{flex-wrap:wrap}#projects .flex .column .technologies .technology{font-size:1.5rem;color:#CA424F;font-weight:bold;line-height:100%;margin:0 12px}#projects .flex .column .links .link{margin:24px 12px}#projects .flex .column .links .link svg{stroke:#3B393C}#projects .padding-project-title{padding-top:50px}#projects .padding-project{padding-top:200px}.text-align-right{text-align:right}@media only screen and (max-width: 900px){#projects{padding:100px 40px 25vh 40px}#projects .flex .column{width:100%}#projects .flex .project-text{position:absolute;padding:1rem;box-sizing:border-box}#projects .flex .project-text .section-subtitle{text-align:center}#projects .flex .project-text .section-title{color:#FCFBF6;text-align:center}#projects .flex .project-text .description{color:#FCFBF6;text-align:center;font-size:1rem}#projects .flex .project-text .technologies{justify-content:center}#projects .flex .project-text .technologies .technology{font-size:1rem;color:#CA424F;font-weight:bold;line-height:100%;margin:0 8px}#projects .flex .project-text .links{justify-content:center}#projects .flex .project-text .links .link{margin:24px 12px}#projects .flex .project-text .links .link svg{stroke:#FCFBF6}#projects .flex .project-image{flex-shrink:0}#projects .flex .project-image .image-container{width:100%}}

#heading{width:100%;min-height:100vh;margin:0 auto;max-width:1920px;padding-left:192px;padding-right:192px;box-sizing:border-box;position:relative;display:flex;justify-content:flex-end;flex-direction:column;-webkit-user-select:none;-ms-user-select:none;user-select:none}#heading .title-container{z-index:2;margin-bottom:20vh}#heading .title-container h1.title{line-height:90%;font-size:10vw;text-transform:uppercase}#heading .title-container h1.title span{font-family:"New York"}#heading .title-container .subtitle{font-size:3vw}#heading .circle{z-index:1;width:50vw;height:50vw;position:absolute;right:-5vw;bottom:-10vw;background:#CA424F;border-radius:50%;box-shadow:0px 14px 20px 0px rgba(202,66,79,0.15)}@media only screen and (max-width: 1330px){#heading .title-container h1.title{line-height:90%;font-size:8rem}#heading .title-container .subtitle{font-size:3rem}}@media only screen and (max-width: 1100px){#heading{display:flex;align-items:center;justify-content:center}#heading .title-container{margin-bottom:0;text-align:center}#heading .title-container h1.title{line-height:90%;font-size:8rem}#heading .title-container .subtitle{font-size:3rem}#heading .circle{width:60vw;height:60vw;right:-25vw;bottom:-5vw}}@media only screen and (max-width: 900px){#heading .title-container h1.title{line-height:90%;font-size:6rem}#heading .title-container .subtitle{font-size:2rem}#heading .circle{width:70vw;height:70vw;right:-25vw;bottom:-5vw}}@media only screen and (max-width: 520px){#heading .title-container h1.title{line-height:90%;font-size:4rem}#heading .title-container .subtitle{font-size:2rem}#heading .circle{width:100vw;height:100vw;right:-50vw;bottom:-5vw}}

#profile{min-height:100vh;padding:100px 192px;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-sizing:border-box;display:flex;justify-content:center;flex-direction:column;overflow:hidden}#profile p{font-size:2rem}@media only screen and (max-width: 900px){#profile{padding-left:40px;padding-right:40px}#profile p{font-size:1rem}}

